import React, {useState} from 'react';
import {
    Card,
} from 'antd';
import {
    ComponentGroupButtons,
} from '../../buildingBlockComponent';

/**
 * ViewCard组件
 * @param props
 *  title: 标题
 *  styles: 样式
 *  content: 内容部分
 *  isShowComponent: 是否是展示组件
 *  footer: 脚部内容
 * @constructor
 */
function Index({
    isShowComponent = false,
    ...props
}) {
    const [isShowCode, setIsShowCode] = useState(false);

    function copyCodeFunc() {
        console.log('copy1')
        document.addEventListener('copy', function(e){
            e.clipboardData.setData('text/plain', props.codeContent || '暂无代码，欢迎您与吴小迪一起维护哦~在右下方联系我哦~');
            e.preventDefault(); // We want our data, not data from any selection, to be written to the clipboard
        });
        document.execCommand('copy')
        console.log('copy2')
    }

    return (
        <div>
            <Card
                title={props.title}
                style={props.styles}
            >
                {props.content}
            </Card>
            {isShowComponent ? (
                <Card
                    title={(
                        <ComponentGroupButtons
                            isShow={isShowCode}
                            changeShowState={() => {setIsShowCode(!isShowCode)}}
                            copyCode={copyCodeFunc}
                        />
                    )}
                    bodyStyle={{
                        padding: isShowCode ? '24px' : 0,
                    }}
                >
                    {isShowCode ? (
                        props.codeContent || '暂无代码，欢迎您与吴小迪一起维护哦~在右下方联系我哦~'
                    ) : null}
                </Card>
            ) : null}
            {props.footer}
        </div>
    );
}

export default Index;



