import React from 'react';

// 设置距离的组件
// 默认向下，12px
// 根据margin的值看上下左右的间距，第一个值为上，而后顺时针到第四个
function Index({ margin = [0, 0, '12px', 0], styles = {}, ...props}) {
    return (
        <div
            style={{
                margin: `${margin[0]} ${margin[1]} ${margin[2]} ${margin[3]} `,
                ...styles,
            }}
        />
    );
}

export default Index;



