import React from 'react';
import {
    Button,
    Icon,
} from 'antd';
import {Gap} from '../index';

/**
 * 组件展示下方的按钮组组件
 * @param props
 *          isShow： 是否展开内容了
 *          changeShowState: 改变父组件的状态
 *          copyCode: 复制代码的按钮
 * @returns {*}
 * @constructor
 */
function Index({
   isShow = false,
   changeShowState,
   copyCode,
   ...props}) {
    return (
        <div style={{display: 'flex'}}>
            {/*如果isShow为true则是显示状态*/}
            {/*点击的时候改变父组件的状态*/}
            <div onClick={changeShowState}>
                {isShow ? (
                    <Button icon={'eye-invisible'}>隐藏代码</Button>
                ) : (<Button icon={'eye'}>查看代码</Button>)}
            </div>
            <Gap margin={[0, '24px', 0, 0]} />
            <Button icon={'copy'} onClick={copyCode}>复制代码</Button>
        </div>
    );
}

export default Index;



